.ChannelListContainer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;
}
.ChannelItem {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;
    direction: ltr;
    width: 300px;
    background-color: #fff;
    border-bottom: black;
    border-bottom-width: 2px;
}

.IconButton {
    padding: 0;
    margin: 0;
    background-color: transparent;
    border: none;
}

.BellIcon {
    width: 50px;
    cursor: pointer;
}

.ChannelDisplay {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    width: 100%;
    padding-right: 10px;
}

.ChannelItem:hover  {
    background-color: #EEEEEE;
    border-color: #EEEEEE;
    cursor: pointer;
}

.ChannelItem:active {
    background-color: #DDD;
    border-color: #DDD;
}

.ActiveChannel {
    background-color: #EEE;
    border-color: #EEE;
}

.ChannelTitle {
    /*font-size: 20px;*/
    text-align:'left';
	margin-left: 10px;
    white-space: nowrap;
    float: left;
    cursor: pointer;
}

.ArrowIcon {
    width: 40px;
    height: 40px;
    float: right;
}

.hidden {
    display: none;
}

@media (max-width:960px) {
    .ChannelTitle {
        font-size: 16px;
    }
    
    .ArrowIcon {
        width: auto;
        height: 40px;
    }

    .BellIcon {
        width: 30px;
        padding: 10px;
    }
}


