.Home {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: start;
        justify-content: flex-start;
}

.Body {
    margin-top: 80px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: start;
        justify-content: flex-start;
}

.logout {
    cursor: pointer;
    margin-right: 20px;
}

.ChannelList {
    position: fixed;
    margin-bottom: 10;
    overflow: auto;
    direction: rtl;
    height: 100%;
    background-color: white;
    border-width: 1px;
    border-color: #EEEEEE;
}

.Channel {
	height: 30px;
	padding: 5px;
}

.Feed {
    -ms-flex-positive: 1;
        flex-grow: 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    margin-left: 300px;
}

.mobileMenuBtn {
    display:none;
    background-color: transparent;
    border: transparent;
    color: #fff;
    font-size: 16px;
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 10px;
    padding-bottom: 10px;
}

.mobileMenuBtn:hover {
    background-color: #fff;
    border-color: #fff;
    color: #009999;
}

.mobileMenuBtnRow {
    width:100%;
    background-color: #009999;
    border-color: #009999;
    display: none;
}

@media (max-width:960px){
    .mobileMenuBtn {
        text-align: center;
        display: block;
    }
    .ChannelList {
        height: auto;
        padding: 10px;
        width: 100%;
        position: relative;
    }

    .hideMobile{
        display: none;
    }
    .Body {
        display: block;
    }

    .Channel {
        height: 15px;
        padding: 5px;
        font-size: 12px;
    }

    .mobileMenuBtnRow {
        display: block;
    }
    .Feed {
        margin-left: 10px;
        margin-right: 10px;
    }
}



