.Header {
	position: fixed;
	top: 0;
	width: 100%;
	height: 80px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	background: -webkit-gradient(linear, left top, left bottom, from(#71d3d1), to(#67bcba));
	background: -webkit-linear-gradient(#71d3d1, #67bcba);
	background: -o-linear-gradient(#71d3d1, #67bcba);
	background: linear-gradient(#71d3d1, #67bcba);
	color: white;
}

.HeaderLeft {
	padding-left: 80px;
}

.HeaderRight {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	padding-right: 80px;
}


