.Main {
	height: 100vh;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: stretch;
	    align-items: stretch;
	-ms-flex-pack: center;
	    justify-content: center;
	font-family: 'Roboto', sans-serif;
	margin-top: 0px;
}

.Login {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	margin-top: 10px;
	height: 80px;
}

.LoginTitle {
	margin-right: 10px;
	margin-left: 5px;
	padding: 0px;
}

.LoginForms {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: end;
	    justify-content: flex-end;
	-ms-flex-align: center;
	    align-items: center;
}

.LoginError {
	color: rgb(179, 21, 21);
	margin-left: 5px;
}

.Register {
	padding-top: 80px;
	-ms-flex: 1 1;
	    flex: 1 1;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: start;
	    align-items: flex-start;
}

.Card {
	padding-left: 30px;
	padding-right: 30px;
	padding-top: 5px;
	padding-bottom: 5px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: start;
	    align-items: flex-start;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	margin: 20px;
	background-color: #FFFFFF;
    -webkit-box-shadow: 0 6px 8px 0 rgba(0, 0, 0, 0.4), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 6px 8px 0 rgba(0, 0, 0, 0.4), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    text-align: center;
}

.mobileLogin {
	padding-top: 90px;
	display: none;
}

@media (max-width: 960px) {
	.mobileLogin {
		display: -ms-flexbox;
		display: flex;
		-ms-flex-pack: center;
		    justify-content: center;
	}

	.Register {
		padding-top: 20px;
	}
}


